import React, { useState } from 'react'
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { url } from '../api/URLbackend';
import NavbarTeacher from '../components/NavbarTeacher'
import axios from 'axios'
import toast from 'react-hot-toast';
const StudentData = () => {

    let navigate = useNavigate();

    const location = useLocation()
    const { Id } = location.state;
    const { Prn } = location.state;
    const { Name } = location.state;
    const { Dob } = location.state;
    const { Mail } = location.state;
    const { Phone } = location.state;
    const { Course } = location.state;
    const { Address } = location.state;
    const { Gender } = location.state;

    const [saveStudentData, setSaveStudentData] = useState()

    const [studentPRN, setStudentPRN] = useState(Prn)
    const [studentName, setStudentName] = useState(Name)
    const [studentdob, setStudentdob] = useState(Dob)
    const [studentMail, setStudentMail] = useState(Mail)
    const [studentPhone, setStudentPhone] = useState(Phone)
    const [course, setCourse] = useState(Course)
    const [studentAddress, setStudentAddress] = useState(Address)
    const [studentGender, setStudentGender] = useState(Gender)

    const handleSave = () => {
        let temp = {
            studentId: Id,
            studentPrn: studentPRN,
            studentName: studentName,
            studentDob: studentdob,
            studentMail: studentMail,
            studentPhone: studentPhone,
            studentCourse: course,
            studentAddress: studentAddress,
            studentGender: studentGender
        }

        setSaveStudentData(temp);

        if (saveStudentData != null) {
            axios.post(url + `updatebysid`, JSON.stringify(saveStudentData),
                {
                    headers: { "Content-Type": "application/json" },
                    params: { sid: Id }, //Add studentID as a param 
                })
                .then(() => {
                    navigate('/Student')
                    toast.success("Student Data Updated >>")
                })
                .catch(function (error) { toast.error("Something wents wrong !") })
        }
    }


    return (
        <div>
            <div className="cont container-fluid p-3 d-flex justify-content-center align-items-center">
                <div className="row" style={{ backgroundColor: "#1f153c", width: "21em" }}>
                    <NavbarTeacher />
                </div>
                <div className="main col-9 d-flex align-items-center flex-column">
                    <div className="upp "> </div>
                    <div className="low ">
                        <div className="help">
                            <h3 className="text-center mt-3">Update the Student</h3>
                        </div>
                        <div className='mt-5' style={{ height: "33em" }}>

                            <div class="row" style={{ height: "33em" }}>
                                <div class="col  ">
                                    <div className='col-7 mt-5 d-flex flex-column ' style={{marginLeft : "3em"}}>

                                        <div className="form-group">
                                            <label htmlFor="" className="fw-bold d-flex flex-start mb-2">studentPrn : </label>
                                            <input type="text" className="form-control" value={studentPRN} onChange={(e) => setStudentPRN(e.target.value)} />
                                        </div>

                                        <div className="form-group">
                                            <label htmlFor="" className="fw-bold d-flex flex-start mb-2">studentName : </label>
                                            <input type="text" className="form-control" value={studentName} onChange={(e) => setStudentName(e.target.value)} />
                                        </div>

                                        <div className="form-group">
                                            <label htmlFor="" className="fw-bold d-flex flex-start mb-2">studentAddress </label>
                                            <input type="text" className="form-control" value={studentAddress} onChange={(e) => setStudentAddress(e.target.value)} />
                                        </div>

                                        <div className="form-group">
                                            <label htmlFor="" className="fw-bold d-flex flex-start mb-2">studentCourse </label>
                                            <input type="text" className="form-control" value={course} onChange={(e) => setCourse(e.target.value)} />
                                        </div>

                                        <div className="form-group">
                                            <label htmlFor="" className="fw-bold d-flex flex-start mb-2">studentMail </label>
                                            <input type="text" className="form-control" value={studentMail} onChange={(e) => setStudentMail(e.target.value)} />
                                        </div>
                                    </div>
                                </div>
                                <div class="col">
                                    <div className='col-7 ms-5 mt-5 d-flex flex-column ' >
                                        <div className="form-group">
                                            <label htmlFor="" className="fw-bold d-flex flex-start mb-2">studentPhone </label>
                                            <input type="text" className="form-control" value={studentPhone} onChange={(e) => setStudentPhone(e.target.value)} />
                                        </div>

                                        <div className="form-group">
                                            <label htmlFor="" className="fw-bold d-flex flex-start mb-2">studentGender </label>
                                            <input type="text" className="form-control" value={studentGender} onChange={(e) => setStudentGender(e.target.value)} />
                                        </div>

                                        <div className="form-group">
                                            <label htmlFor="" className="fw-bold d-flex flex-start mb-2">studentDob </label>
                                            <input type="date" className="form-control" value={studentdob} onChange={(e) => setStudentdob(e.target.value)} />
                                        </div>
                                    </div>
                                    <abbr title='Go back'>
                                        <Link to="/Student">
                                            <button className='col-1 btn btn-outline-primary' style={{ borderRadius: "50%", position: "relative", top: "10em", left: "-23em" }}>
                                                <i class="bi bi-caret-left-fill"></i>
                                            </button>
                                        </Link>
                                    </abbr>
                                    <abbr title='Update the data'>
                                        <button onClick={() => handleSave()} className='col-1 btn btn-outline-primary' style={{ borderRadius: "50%", position: "relative", top: "10em", left: "-22em" }}>
                                            <i class="bi bi-check-lg"></i>
                                        </button>
                                    </abbr>
                                </div>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default StudentData;