import React, { useEffect, useState } from 'react'
import NavbarTeacher from '../components/NavbarTeacher'
import { url } from '../api/URLbackend'
import axios from 'axios'

export default function ExpertiseT() {

    const [expertise, setExpertise] = useState([])

    useEffect(() => {
        axios.get(url + `showexpertise`)
            .then((res) => {
                setExpertise(res.data)
            })
    }, [])



    return (
        <div >
            <div className="cont container-fluid p-3 d-flex justify-content-center align-items-center">
                <div className="row" style={{ backgroundColor: "#1f153c", width: "21em" }}>
                    <NavbarTeacher />
                </div>
                <div className="main col-9 d-flex align-items-center flex-column">
                    <div className="upp "></div>

                    <div className="low ">
                        <div className="heading mt-3">
                            <h3 style={{ fontFamily: "revert" }} > Expertise</h3>
                        </div>
                        <div className="d-flex flex-column justify-content-end" style={{ width: "6em", marginLeft: "62em" }}>


                        </div>
                        <div className="tableformat" style={{ height: "27em", marginTop: "8em", marginLeft: "8em", overflow: "auto", overflowX: "hidden" }} >
                            <table className="table table-dark">
                                <thead>
                                    <tr>
                                        <th scope="col">Name</th>
                                        <th scope="col">Subject</th>
                                        <th scope="col">Mail </th>
                                        <th scope="col">Year Of Experience</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {
                                        expertise.map((data) => {
                                            return (
                                                <tr key={data.teacherId}>
                                                    <th scope="row">{data.teacherName}</th>
                                                    <td>{data.teacherSubject}</td>
                                                    <td>{data.teacherMail}</td>
                                                    <td>{data.yrOfExp}</td>
                                                </tr>
                                            )
                                        })
                                    }
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
