import { Link, useNavigate } from 'react-router-dom'


const NavbarTeacher = () => {

  let navigate = useNavigate();
  const handleLogOut =()=>{
      localStorage.clear();
      navigate("/")
  }

  return (
    <div>
      <div className="navb d-inline">
        <div className="logo d-flex flex-row">
          <img src="/images/logo.png" alt='someimg' className="commonlogo" />
          <div className="orgname text-light d-flex justify-content-center align-items-center flex-column">
            <h4> OERM </h4>
          </div>
        </div>
        <div className="navvbar text-light d-flex flex-column justify-content-center ">
          <div className="eachBox"><Link to="/DashBoardTeacher" > DashBoard </Link></div>
          <div className="eachBox"><Link to="/Student"          > Student </Link></div>
          <div className="eachBox"><Link to="/TeacherProfile"   > Profile      </Link></div>
          <div className="eachBox"><Link to="/Announcement"     > Announcement </Link></div>
          <div className="eachBox"><Link to="/ExpertiseT"       > Expertise    </Link></div>
          <div className="eachBox"><Link to="/AboutTeacher"     > About        </Link></div>
          <div className="eachBox btnn">
            <button type="submit" className="eachBtn" form="form1" value="logOut" onClick={handleLogOut}><i
              className="bi bi-box-arrow-right"></i>&nbsp;Log-Out
            </button>
          </div>
        </div>
      </div>
    </div>
  )
}

export default NavbarTeacher