import axios from 'axios';
import React, { useState } from 'react'
import toast from 'react-hot-toast';
import { Link, useNavigate } from 'react-router-dom';
import { url } from '../api/URLbackend';
import NavbarTeacher from '../components/NavbarTeacher';

const AddStudent = () => {

    let navigate = useNavigate();

    const [username, setUsername] = useState()
    const [password, setPassword] = useState()

    const [studentPRN, setStudentPRN] = useState()
    const [studentName, setStudentName] = useState()
    const [studentdob, setStudentdob] = useState()
    const [studentMail, setStudentMail] = useState()
    const [studentPhone, setStudentPhone] = useState()
    const [course, setCourse] = useState()
    const [studentAddress, setStudentAddress] = useState()
    const [studentGender, setStudentGender] = useState()

    const [user, setUser] = useState('')
    const [saveStudentData, setSaveStudentData] = useState()

    const handleSave = () => {

        axios.post(url + `find`, null, { params: { username: username } })
            .then((res) => setUser(res.data));

        if (user === username) {
            toast.error("Username already present")
            setUsername();
            return;
        }

        if (user !== username) {
            let temp = {
                studentPrn: studentPRN,
                studentName: studentName,
                studentDob: studentdob,
                studentMail: studentMail,
                studentPhone: studentPhone,
                studentCourse: course,
                studentAddress: studentAddress,
                studentGender: studentGender
            }

            const sendDataToUser = async (tempUser) => {
                await axios.post(url + `register`, tempUser)
                    .then(() => {
                        navigate('/Student')
                        toast.success("Username and Password Added Success >> ")
                    })
                    .catch(function (error) { toast.error("Something Wents Wrong &&&") })
            }

            setSaveStudentData(temp);

            if (saveStudentData != null) {
                axios.post(url + `addstudentbyteacher`, saveStudentData)
                    .then((res) => {
                        let tempUser = {
                            username: username,
                            password: password,
                            phone: studentPhone,
                            role: "student",
                            tId: {
                                teacherId: 0
                            },
                            sId: {
                                studentId: res.data
                            }

                        }
                        toast.success("Student Added Success >>")
                        sendDataToUser(tempUser);
                    })
                    .catch(function (error) {  })
            }
        }

    }

    return (
        <div>
            <div className="cont container-fluid p-3 d-flex justify-content-center align-items-center">
                <div className="row" style={{ backgroundColor: "#1f153c", width: "21em" }}>
                    <NavbarTeacher />
                </div>
                <div className="main col-9 d-flex align-items-center flex-column">
                    <div className="upp "> </div>
                    <div className="low ">
                        <div className="help">
                            <h3 className="text-center mt-3">Add new Student Here</h3>                        </div>
                        <div className='mt-5' style={{ height: "33em" }}>

                            <div class="row" style={{ height: "33em" }}>
                                <div class="col  ">
                                    <div className='col-7 mt-5 d-flex flex-column ' style={{ marginLeft: "3em" }}>

                                        <div className="form-group">
                                            <label htmlFor="" className="fw-bold d-flex flex-start mb-2">studentPrn : </label>
                                            <input placeholder='must be a number' type="text" className="form-control" value={studentPRN} onChange={(e) => setStudentPRN(e.target.value)} required />
                                        </div>

                                        <div className="form-group">
                                            <label htmlFor="" className="fw-bold d-flex flex-start mb-2">studentName : </label>
                                            <input type="text" className="form-control" value={studentName} onChange={(e) => setStudentName(e.target.value)} required />
                                        </div>

                                        <div className="form-group">
                                            <label htmlFor="" className="fw-bold d-flex flex-start mb-2">studentAddress </label>
                                            <input type="text" className="form-control" value={studentAddress} onChange={(e) => setStudentAddress(e.target.value)} />
                                        </div>

                                        <div className="form-group">
                                            <label htmlFor="" className="fw-bold d-flex flex-start mb-2">studentCourse </label>
                                            <input type="text" className="form-control" value={course} onChange={(e) => setCourse(e.target.value)} />
                                        </div>

                                        <div className="form-group">
                                            <label htmlFor="" className="fw-bold d-flex flex-start mb-2">studentMail </label>
                                            <input type="text" className="form-control" value={studentMail} onChange={(e) => setStudentMail(e.target.value)} />
                                        </div>
                                    </div>
                                </div>
                                <div class="col">
                                    <div className='col-7 ms-5 mt-5 d-flex flex-column ' >
                                        <div className="form-group">
                                            <label htmlFor="" className="fw-bold d-flex flex-start mb-2">studentPhone </label>
                                            <input type="text" className="form-control" value={studentPhone} onChange={(e) => setStudentPhone(e.target.value)} />
                                        </div>

                                        <div className="form-group">
                                            <label htmlFor="" className="fw-bold d-flex flex-start mb-2">studentGender </label>
                                            <input placeholder='male or female or other' type="text" className="form-control" value={studentGender} onChange={(e) => setStudentGender(e.target.value)} />
                                        </div>

                                        <div className="form-group">
                                            <label htmlFor="" className="fw-bold d-flex flex-start mb-2">studentDob </label>
                                            <input type="date" className="form-control" value={studentdob} onChange={(e) => setStudentdob(e.target.value)} />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="" className="fw-bold d-flex flex-start mb-2">UserName : </label>
                                            <input type="text" className="form-control" value={username} onChange={(e) => setUsername(e.target.value)} required />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="" className="fw-bold d-flex flex-start mb-2">Password : </label>
                                            <input type="text" className="form-control" value={password} onChange={(e) => setPassword(e.target.value)} required />
                                        </div>



                                    </div>
                                    <abbr title='Go back'>
                                        <Link to="/Student">
                                            <button className='col-1 btn btn-outline-primary' style={{ borderRadius: "50%", position: "relative", top: "-1em", left: "-23em" }}>
                                                <i class="bi bi-caret-left-fill"></i>
                                            </button>
                                        </Link>
                                    </abbr>
                                    <abbr title='Update the data'>
                                        <button onClick={() => handleSave()} className='col-1 btn btn-outline-primary' style={{ borderRadius: "50%", position: "relative", top: "-1em", left: "-22em" }}>
                                            <i class="bi bi-check-lg"></i>
                                        </button>
                                    </abbr>
                                </div>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default AddStudent;