import axios from 'axios';
import React, { useState, useEffect } from 'react'
import toast from 'react-hot-toast';
import { url } from '../api/URLbackend';
import NavbarTeacher from '../components/NavbarTeacher';

const TeacherProfile = () => {

    const [disable, setDisable] = useState(true)
    const [toggleBtn, setToggleBtn] = useState(true)
    const [saveTeacherData, setsaveTeacherData] = useState()

    const [tid, setTid] = useState(localStorage.getItem("teacherId"))
    const [teacherName, setTeacherName] = useState('')
    const [teacherdob, setTeacherdob] = useState('')
    const [teacherMail, setTeacherMail] = useState('')
    const [teacherPhone, setTeacherPhone] = useState('')
    const [yrOfExp, setYrOfExp] = useState(0)
    const [teacherAddress, setTeacherAddress] = useState('')
    const [techersubject, setTechersubject] = useState()
    const [teachergender, setTeachergender] = useState()


    const [showHandlePicture, setShowHandlePicture] = useState(false)
    const [ProfileImage, setProfileImage] = useState()

    useEffect(() => {
        axios.post(url + `tid`, null, { params: { tid: tid } })
            .then((res) => {
                setTid(res.data.teacherId);
                setTeacherName(res.data.teacherName);
                setTeacherAddress(res.data.teacherAddress);
                setTeacherdob(res.data.teacherDob);
                setTeacherMail(res.data.teacherMail);
                setTeacherPhone(res.data.teacherPhone);
                setYrOfExp(res.data.yrOfExp);
                setTechersubject(res.data.teacherSubject);
                setTeachergender(res.data.teacherGender)
                setProfileImage(res.data.image)
            })
            .catch(function (error) { toast.error("Something Wents Wrong") })
    }, [tid])

    const handleSave = () => {
        let temp = {
            teacherId: tid,
            teacherName: teacherName,
            teacherDob: teacherdob,
            teacherMail: teacherMail,
            teacherPhone: teacherPhone,
            teacherSubject: techersubject,
            yrOfExp: yrOfExp,
            teacherAddress: teacherAddress,
            teacherGender: teachergender
        }

        setsaveTeacherData(temp);
        
        if (saveTeacherData != null) {
            axios.post(url + `updatebytid`, JSON.stringify(saveTeacherData),
            {
                headers: { "Content-Type": "application/json" },
                params: { tid: tid }, //Add teacherID as a param 
            })
            .then(() => {
                toast.success("User Updated success >>")
            })
            .catch(function (error) { toast.error("Something wents wrong !") })
            
            setToggleBtn(true);
            setDisable(true);
        }
    }

    const handleEdit = () => {
        setDisable(false);
        setToggleBtn(false)
    }

    const [imgFile, setImgFile] = useState()

    const converToBase64 = (file) => {
        return new Promise((resolve, reject) => {
            const fileReader = new FileReader();
            fileReader.readAsDataURL(file);
            fileReader.onload = () => {
                resolve(fileReader.result);
            };
            fileReader.onerror = (error) => {
                reject(error);
            };
        });
    };

    const handleAdd = async (e) => {
        const file = e.target.files[0]
        const base64 = await converToBase64(file)
        setImgFile(base64);
    }

    const handleSavePic = () => {
        if (imgFile !== null) {

            let temp = {
                file: imgFile
            }

            axios.post(url + `addprofilepicteacher`, temp, {
                params: { imgtid: localStorage.getItem("teacherId") },
            })
                .then((res) => {
                    setProfileImage(res.data)
                })
                .catch(function (error) {
                    console.log(error);
                })
            setShowHandlePicture(!showHandlePicture)
        }
        toast.success("Profile Picture Updated >>")
    }

    const handleRemove = () => {
        axios.post(url + `deleteprofilepicteacher`, {
            params: { imgtid: localStorage.getItem("teacherId") },
        })
            .then((res) => {
                toast.success("Profile Picture Deleted >>")
            })
            .catch(function (error) {
                toast.error("Something wents wrong !")
            })
    }


    return (
        <div className="cont container-fluid p-3 d-flex justify-content-center align-items-center">

            <div className="row" style={{ backgroundColor: "#1f153c", width: "21em" }}>
                <NavbarTeacher />
            </div>
            <div className="main col-9 d-flex align-items-center flex-column">
                <div className="upp ">
                    <img src="/images/ProfileBag.jpg" alt="bg-img" className="uppImg" />
                </div>
                <div className="low ">
                    <div className="picture">

                        {
                            !showHandlePicture ? ProfileImage === null ? <img src="/images/logo.png" alt="profile"
                                style={{ height: "8em", width: "10em", borderRadius: "50%" }} /> : <img src={ProfileImage} alt="profile"
                                    style={{ height: "8em", width: "10em", borderRadius: "50%" }} />
                                :
                                <div className='border border-danger d-flex flex-column align-items-center justify-content-center' style={{ height: "8em", width: "11em", borderRadius: "50%", backgroundColor: "black" }} >
                                    <button className='border border-none mb-3' style={{ backgroundColor: "black", width: "2em", height: "26px", borderRadius: "31%" }} onClick={() => handleRemove()}> <i class="bi bi-trash"></i> </button>
                                    <label style={{ backgroundColor: "black", width: "3em", height: "26px", borderRadius: "31%" }}>
                                        <input className='btn btn-info d-none' onChange={(e) => handleAdd(e)} type='file' label="Image" /><h3><i class="bi bi-plus-circle-dotted text-white"></i></h3>
                                    </label>
                                    <button onClick={() => handleSavePic()} style={{ border: "none", backgroundColor: "#0d6efd", width: "2em", borderRadius: "51%", position: "relative", top: "22px", left: "-2em" }} ><i class="bi bi-check2-all"></i></button>
                                </div>

                        }
                    </div>
                    <button className='btn btn-primary ' onClick={() => setShowHandlePicture(!showHandlePicture)} style={{ width: "2em", borderRadius: "51%", position: "relative", top: "-5em", left: "-24em", zIndex: "40" }}><i class="bi bi-pencil"></i></button>


                    <div className="col-5" style={{ position: "relative", top: "-4em", left: "16em" }}>
                        <input type="text" className='col-12 ps-5' value={techersubject} disabled={disable} name="teachersub" onChange={(e) => setTechersubject(e.target.value)} style={{ backgroundColor: "transparent", borderRadius: "0", border: "none", borderBottomStyle: "ridge" }} />
                    </div>
                    <div className="pictureBelow" style={{ height: "52vh" }}>
                        <div className="leftDiv">
                            <div className="leftblock">
                                <div className="form-group">
                                    <label htmlFor="teacherid" className="fw-bold">Teacher Id : </label>
                                    <input type="text" className="form-control" value={tid} disabled={disable} name="teacherid" placeholder="Id" />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="teacherName" className="fw-bold">Teacher Name : </label>
                                    <input type="text" className="form-control" value={teacherName} disabled={disable} name="teacherName" onChange={(e) => setTeacherName(e.target.value)} placeholder="Enter Your Name" />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="dob" className="fw-bold">Date of Birth : </label>
                                    <input type="text" className="form-control" value={teacherdob} disabled={disable} onChange={(e) => setTeacherdob(e.target.value)} name="dob" />
                                </div>
                                <div className="checkboxx d-flex flex-row">
                                    <div className="form-check">
                                        <label className="form-check-label me-3 fw-bold" htmlFor="gender">Male</label>
                                        <input type="radio" className="form-check-input" name="gender" value="male" disabled={disable} checked={teachergender === "male"}
                                            onChange={(e) => setTeachergender(e.target.value)} />
                                    </div>
                                    <div className="form-check">
                                        <label className="form-check-label me-3 fw-bold" htmlFor="gender">Female</label>
                                        <input type="radio" className="form-check-input" name="gender" value="female" disabled={disable} checked={teachergender === "female"}
                                            onChange={(e) => setTeachergender(e.target.value)} />
                                    </div>
                                    <div className="form-check">
                                        <label className="form-check-label fw-bold" htmlFor="gender">Other</label>
                                        <input type="radio" className="form-check-input" name="gender" value="other" disabled={disable} checked={teachergender === "other"}
                                            onChange={(e) => setTeachergender(e.target.value)} />
                                    </div>
                                </div>
                            </div>
                            {

                                toggleBtn ?
                                    <button type="submit" className="submitbtnleft btn btn-outline-primary" onClick={() => handleEdit()} style={{ width: "7em" }}><i
                                        className="bi bi-pencil"></i>&nbsp; Edit</button>

                                    :
                                    <button type="submit" className="submitbtnleft btn btn-outline-success" onClick={() => handleSave()} style={{ width: "7em" }}><i
                                        className="bi bi-check-all"></i>&nbsp; Save</button>

                            }
                        </div>
                        <div className="rightDiv">
                            <div className="rightblock">
                                <div className="form-group">
                                    <label htmlFor="teacherMail" className="fw-bold">Teacher Mail : </label>
                                    <input type="email" className="form-control" value={teacherMail} disabled={disable} name="teacherMail" onChange={(e) => setTeacherMail(e.target.value)}
                                        placeholder="Enter Your Mail id" />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="teacherPh" className="fw-bold">Phone no : </label>
                                    <input type="tel" className="form-control" value={teacherPhone} disabled={disable} name="teacherPhone" onChange={(e) => setTeacherPhone(e.target.value)} placeholder="Enter Your Name" />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="yrofexp" className="fw-bold">Year of Experience : </label>
                                    <input type="text" className="form-control" id="yrofexp" disabled={disable} value={yrOfExp} onChange={(e) => setYrOfExp(e.target.value)} />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="teacherAdd " className="fw-bold">Address : </label>
                                    <input type="text" className="form-control" value={teacherAddress} disabled={disable} onChange={(e) => setTeacherAddress(e.target.value)} name="teacherAddress" />
                                </div>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default TeacherProfile