import '../css/styleguage.css'
import React, { useEffect, useState } from 'react'
import NavbarStudent from '../components/NavbarStudent'
import { useSelector } from 'react-redux'
import { PopoverBody, PopoverHeader, Popover } from 'reactstrap'
import axios from 'axios'
import { url } from '../api/URLbackend'
import toast from 'react-hot-toast'

const DashBoardStudent = () => {

  const AnnounceDash = useSelector((states) => states.AnnounceRed)
  const [overallAssign, setOverallAssign] = useState(0)

  useEffect(() => {
    axios.post(url + `assignmentscorebysid`, null, { params: { sid: localStorage.getItem("studentId") } })
      .then((res) => {
        let data = res.data
        setOverallAssign(data)
      })
      .catch(function (error) {
        toast.error("Something wents wrong !");
      })


  }, [])

  const handleUpdateScore = () => {
    if (overallAssign !== 0) {
      console.log(overallAssign)
      axios.post(url + `saveoverallscore`, null, { params: { sid: localStorage.getItem("studentId"), score: overallAssign } })
        .then((res) => {
          toast.success("Score Updated !")
        })
        .catch(function (error) { })
    }else{
      toast.error("Try After Few Minute !!")
    }
  }

  const [popoverOpen, setPopoverOpen] = React.useState(false);
  const toggle = () => setPopoverOpen(!popoverOpen);

  return (
    <div className="cont container-fluid p-3 d-flex justify-content-center align-items-center" >
      <div className="row" style={{ backgroundColor: "#1f153c", width: "21em" }}>
        <NavbarStudent />
      </div>
      <div className="main col-9 d-flex align-items-center flex-column">
        <div className="upp ">
          <div>
            <div className="text-light mt-5 col-2 d-inline-block" style={{ marginRight: "18em" }}>
              <h4>DashBoard &nbsp; <i class="bi bi-reception-4"></i> </h4>
            </div>
            <div className=" text-light col-6 float-end d-flex justify-content-between mt-4 me-2" style={{ height: "6em" }}>
              <h5 className='mt-4 text-info' id="Popover" type="button" >
                <i class="bi bi-megaphone-fill"></i>

                <Popover placement="bottom" isOpen={popoverOpen} target="Popover" toggle={toggle} >
                  <PopoverHeader className='text-primary'>Announcement for Student</PopoverHeader>
                  <PopoverBody >
                    {
                      AnnounceDash.map((msg) => (
                        <div key={msg.AnnounceToS.length}>
                          <p style={{ fontSize: "larger" }}>{msg.AnnounceToS}</p>
                        </div>
                      ))}

                  </PopoverBody>
                </Popover>
              </h5>

              <div className='d-flex' style={{ height: "6em", width: "18em" }}>

                <h5 className="mt-4 me-4">Hi, {`${localStorage.getItem("studentName")}`}</h5>
                {/* <img src="/images/logo.png" alt="profile photo"
                  style={{ height: "6em", width: "6em", borderRadius: "50%" }} /> */}
              </div>
            </div>
          </div>
        </div>
        <div className=" low d-flex flex-column">

          <div className="col-4 float-start ms-3 mt-3">
            <h3>Welcome Back!</h3>
            <p> It's glad to have you on board</p>
            <hr />

          </div>
          <div>
            <img src="/images/Dashbord.png" alt="student-i" style={{ height: "41vh", width: "23vw", position: "relative", left: "23em", top: "-6em", zIndex: "20" }} />
          </div>
          <div style={{ height: "61vh", position: "relative", top: "-18em" }}>
            <div className="col-6 d-flex ms-5 " style={{ height: "10em" }}>

              <div className="circular me-5" >
                <div className="inner"></div>
                <div className="outer"></div>
                <div className="numb">
                  {overallAssign}%
                </div>
                <div className="circle">
                  <div className="dot">
                    <span></span>
                  </div>
                  <div className="bar left">
                    <div className="progress"></div>
                  </div>
                  <div className="bar right">
                    <div className="progress"></div>
                  </div>
                </div>
                <div className="col-12 d-flex flex-rowms-2" style={{ height: "3em" }}>
                  <p style={{ fontFamily: "cursive", position: "relative", top: "7em" }}>Assignment Percentage
                    <button className='col-4 btn btn-primary' style={{borderRadius : "50%" , position: "relative", top: "-3em" ,left: "5em"}} onClick={() => handleUpdateScore()}><i class="bi bi-check-all"></i></button>
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default DashBoardStudent