import axios from 'axios';
import React, { useState, useEffect } from 'react'
import toast from 'react-hot-toast';
import { url } from '../api/URLbackend';
import NavbarStudent from '../components/NavbarStudent';

const StudentProfile = () => {

    const [disable, setDisable] = useState(true)
    const [toggleBtn, setToggleBtn] = useState(true)
    const [saveStudentData, setSaveStudentData] = useState()

    const [sid, setSid] = useState(localStorage.getItem("studentId"))
    const [studentPRN, setStudentPRN] = useState()
    const [studentName, setStudentName] = useState('')
    const [studentdob, setStudentdob] = useState('')
    const [studentMail, setStudentMail] = useState('')
    const [studentPhone, setStudentPhone] = useState('')
    const [course, setCourse] = useState()
    const [studentAddress, setStudentAddress] = useState('')
    const [studentGender, setStudentGender] = useState()

    const [showHandlePicture, setShowHandlePicture] = useState(false)
    const [ProfileImage, setProfileImage] = useState()

    useEffect(() => {
        axios.post(url + `sid`, null, { params: { sid: sid } })
            .then((res) => {
                setSid(res.data.studentId)
                setStudentPRN(res.data.studentPrn)
                setStudentName(res.data.studentName)
                setStudentdob(res.data.studentDob)
                setStudentMail(res.data.studentMail)
                setStudentPhone(res.data.studentPhone)
                setCourse(res.data.studentCourse)
                setStudentAddress(res.data.studentAddress)
                setStudentGender(res.data.studentGender)
                setProfileImage(res.data.image)

            })
            .catch(function (error) { console.log(error); })
    }, [sid])

    const handleSave = () => {
        let temp = {
            studentId: sid,
            studentPrn: studentPRN,
            studentName: studentName,
            studentDob: studentdob,
            studentMail: studentMail,
            studentPhone: studentPhone,
            studentCourse: course,
            studentAddress: studentAddress,
            studentGender: studentGender
        }

        setSaveStudentData(temp);
        setDisable(true);

        if (saveStudentData != null) {
            axios.post(url + `updatebysid`, JSON.stringify(saveStudentData),
                {
                    headers: { "Content-Type": "application/json" },
                    params: { sid: sid }, //Add studentID as a param 
                })
                .then(() => {
                    setToggleBtn(true)
                    toast.success("User is Upated >> ")
                })
                .catch(function (error) { toast.error("Something wents wrong !") })
        }
    }

    const handleEdit = () => {
        setDisable(false);
        setToggleBtn(false)
    }



    const [imgFile, setImgFile] = useState()

    const converToBase64 = (file) => {
        return new Promise((resolve, reject) => {
            const fileReader = new FileReader();
            fileReader.readAsDataURL(file);
            fileReader.onload = () => {
                resolve(fileReader.result);
            };
            fileReader.onerror = (error) => {
                reject(error);
            };
        });
    };

    const handleAdd = async (e) => {
        const file = e.target.files[0]
        const base64 = await converToBase64(file)
        setImgFile(base64);
    }

    const handleSavePic = () => {
        if (imgFile !== null) {

            let temp = {
                file: imgFile
            }

            axios.post(url + `addprofilepic`, temp, {
                params: { imgsid: localStorage.getItem("studentId") },
            })
                .then((res) => {
                    setProfileImage(res.data)
                })
                .catch(function (error) {
                    console.log(error);
                })
            setShowHandlePicture(!showHandlePicture)
            toast.success("Profile Picture Updated >> ")
        }
    }

    const handleRemove = () => {
        axios.post(url + `deleteprofilepic`, {
            params: { imgsid: localStorage.getItem("studentId") },
        })
            .then((res) => {
                toast.success("Deleted successful >> ")
            })
            .catch(function (error) {
                toast.error("Something wents wrong !")
            })
    }



    return (
        <div className="cont container-fluid p-3 d-flex justify-content-center align-items-center">

            <div className="row" style={{ backgroundColor: "#1f153c", width: "21em" }}>
                <NavbarStudent />
            </div>
            <div className="main col-9 d-flex align-items-center flex-column">
                <div className="upp ">
                    <img src="/images/ProfileBag.jpg" alt="bg-img" className="uppImg" />
                </div>
                <div className="low ">
                    <div className="picture">

                        {
                            !showHandlePicture ? ProfileImage === null ? <img src="/images/logo.png" alt="profile"
                                style={{ height: "8em", width: "10em", borderRadius: "50%" }} /> : <img src={ProfileImage} alt="profile"
                                    style={{ height: "8em", width: "10em", borderRadius: "50%" }} />
                                :
                                <div className='border border-danger d-flex flex-column align-items-center justify-content-center' style={{ height: "8em", width: "11em", borderRadius: "50%", backgroundColor: "black" }} >
                                    <button className='border border-none mb-3' style={{ backgroundColor: "black", width: "2em", height: "26px", borderRadius: "31%" }} onClick={() => handleRemove()}> <i class="bi bi-trash"></i> </button>
                                    <label style={{ backgroundColor: "black", width: "3em", height: "26px", borderRadius: "31%" }}>
                                        <input className='btn btn-info d-none' onChange={(e) => handleAdd(e)} type='file' label="Image" /><h3><i class="bi bi-plus-circle-dotted text-white"></i></h3>
                                    </label>
                                    <button onClick={() => handleSavePic()} style={{ border: "none", backgroundColor: "#0d6efd", width: "2em", borderRadius: "51%", position: "relative", top: "22px", left: "-2em" }} ><i class="bi bi-check2-all"></i></button>
                                </div>

                        }
                    </div>
                    <button className='btn btn-primary ' onClick={() => setShowHandlePicture(!showHandlePicture)} style={{ width: "2em", borderRadius: "51%", position: "relative", top: "-5em", left: "-24em", zIndex: "40" }}><i class="bi bi-pencil"></i></button>



                    <div className="pictureBelow" style={{ height: "58vh" }}>
                        <div className="leftDiv">
                            <div className="leftblock">
                                <div className="form-group">
                                    <label htmlFor="studentId" className="fw-bold">Student PRN : </label>
                                    <input type="text" className="form-control" value={studentPRN} disabled />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="studentName" className="fw-bold">Student Name : </label>
                                    <input type="text" className="form-control" value={studentName} disabled={disable} name="studentName" onChange={(e) => setStudentName(e.target.value)} placeholder="Enter Your Name" />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="dob" className="fw-bold">Date of Birth : </label>
                                    <input type="text" className="form-control" value={studentdob} disabled={disable} onChange={(e) => setStudentdob(e.target.value)} name="dob" />
                                </div>
                                <div className="checkboxx d-flex flex-row">
                                    <div className="form-check">
                                        <label className="form-check-label me-3 fw-bold" htmlFor="gender">Male</label>
                                        <input type="radio" className="form-check-input" name="gender" value="male" disabled={disable} checked={studentGender === "male"}
                                            onChange={(e) => setStudentGender(e.target.value)} />
                                    </div>
                                    <div className="form-check">
                                        <label className="form-check-label me-3 fw-bold" htmlFor="gender">Female</label>
                                        <input type="radio" className="form-check-input" name="gender" value="female" disabled={disable} checked={studentGender === "female"}
                                            onChange={(e) => setStudentGender(e.target.value)} />
                                    </div>
                                    <div className="form-check">
                                        <label className="form-check-label fw-bold" htmlFor="gender">Other</label>
                                        <input type="radio" className="form-check-input" name="gender" value="other" disabled={disable} checked={studentGender === "other"}
                                            onChange={(e) => setStudentGender(e.target.value)} />
                                    </div>
                                </div>
                            </div>
                            {

                                toggleBtn ?
                                    <button type="submit" className="submitbtnleft btn btn-outline-primary" style={{ width: "8em" }} onClick={() => handleEdit()}><i
                                        className="bi bi-pencil"></i>&nbsp; Edit</button>

                                    :
                                    <button type="submit" className="submitbtnleft btn btn-outline-success" style={{ width: "8em" }} onClick={() => handleSave()}><i
                                        className="bi bi-check-all"></i>&nbsp; Save</button>

                            }
                        </div>
                        <div className="rightDiv">
                            <div className="rightblock">
                                <div className="form-group">
                                    <label htmlFor="studentMail" className="fw-bold">Student Mail : </label>
                                    <input type="email" className="form-control" value={studentMail} disabled={disable} name="studentMail" onChange={(e) => setStudentMail(e.target.value)}
                                        placeholder="Enter Your Mail id" />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="studentPh" className="fw-bold">Phone no : </label>
                                    <input type="tel" className="form-control" value={studentPhone} disabled={disable} name="studentPhone" onChange={(e) => setStudentPhone(e.target.value)} placeholder="Enter Your Name" />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="course" className="fw-bold">Course : </label>
                                    <input type="text" className="form-control" id="course" value={course} disabled />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="studentAdd " className="fw-bold">Address : </label>
                                    <input type="text" className="form-control" value={studentAddress} disabled={disable} onChange={(e) => setStudentAddress(e.target.value)} name="studentAddress" />
                                </div>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default StudentProfile;