import '../css/styleguage.css'
import { useEffect } from 'react';
import React from 'react'
import NavbarTeacher from '../components/NavbarTeacher';
import { useSelector } from 'react-redux';
import { Popover, PopoverBody, PopoverHeader } from 'reactstrap'
import axios from 'axios';
import { url } from '../api/URLbackend';
import { useState } from 'react';

export default function DashBoardTeacher() {

    const getMessageHelp = useSelector((state) => state.help)

    const [studentData, setStudentData] = useState(0)

    const [popoverOpen, setPopoverOpen] = React.useState(false);
    const toggle = () => setPopoverOpen(!popoverOpen);

    useEffect(() => {
        axios.post(url + `showallstudent`)
            .then((res) => {
                setStudentData(res.data);
            })
    }, [])

    return (
        <div>
            <div className="cont container-fluid p-3 d-flex justify-content-center align-items-center">
                <div className="row" style={{ backgroundColor: "#1f153c", width: "21em" }}>
                    <NavbarTeacher />
                </div>

                <div className="main col-9 d-flex align-items-center flex-column">
                    <div className="upp ">
                        <div>
                            <div className="text-light mt-5 col-2 d-inline-block" style={{ marginRight: "23em" }}>
                                <h4>DashBoard </h4>
                            </div>
                            <div className=" text-light col-6 float-end d-flex justify-content-around mt-4 " style={{ height: "6em" }}>
                                <a href='https://accounts.google.com/signin' target="_blank" rel="noreferrer"> <button className="btn btn-outline-info pt-1 mt-4 ml-5" style={{ height: "-21em" }}><i
                                    className="bi bi-envelope"></i></button></a>
                                <div className="d-flex">
                                    <em> <h5 className="mt-4 me-4">Hi, {`${localStorage.getItem("teacherName")}`}  </h5></em>
                                    {/* <img src="/images/logo.png" alt="profile photo"
                                        style={{ height: "6em", width: "6em", borderRadius: "50%" }} /> */}
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className=" low d-flex flex-column">

                        <div className="col-4 float-start ms-3 mt-3">
                            <em>
                                <h3>Welcome Back!</h3>
                                <p> It's glad to have you on board</p>
                            </em>
                            <hr />
                        </div>
                        <div>
                            <img src="/images/Dashbord.png" alt="student-i" style={{ height: "41vh", width: "23vw", position: "relative", left: "23em", top: "-6em", zIndex: "20" }} />
                        </div>
                        <div style={{ height: "21vh", position: "relative", top: "-20em" }}>
                            <div className=" col-6 d-flex ms-5 mt-2" style={{ height: "8em" }}>

                                {
                                    `${localStorage.getItem("yrOfExp")}` <= 9 ?
                                        <>
                                            <img src="/images/yrofexp.png" alt="student-i" className=' me-5' style={{ height: "7em" }} />
                                            <b className='text-white' style={{ position: "relative", top: "1em", left: "-116px", fontSize: "2em" }} >{`${localStorage.getItem("yrOfExp")}`}</b>
                                        </>
                                        : <>
                                            <img src="/images/yrofexp.png" alt="student-i" className=' me-5' style={{ height: "7em" }} />
                                            <b className='text-white' style={{ position: "relative", top: "1em", left: "-129px", fontSize: "2em" }} >{`${localStorage.getItem("yrOfExp")}`}</b>
                                        </>
                                }

                            </div>
                            <div className="col-6 d-flex ms-5" style={{ height: "3em" }}>
                                <pre style={{ fontFamily: "cursive" }}>Year Of Experience</pre>

                            </div>

                            <div style={{ position: "relative", top: "232px", left: "66em", width: "3em" }}>
                                <h3 id="Popover" type="button">
                                    <i class="bi bi-bug" ></i>

                                    <Popover placement="left" isOpen={popoverOpen} target="Popover" toggle={toggle} >
                                        <PopoverHeader className='text-primary'>Student Doubts</PopoverHeader>
                                        <PopoverBody >
                                            {
                                                getMessageHelp.map((abc) => (
                                                    <div key={abc.helpMessage.length}>
                                                        <p style={{ fontSize: "larger" }}>{abc.helpMessage}</p>
                                                    </div>
                                                ))}
                                        </PopoverBody>
                                    </Popover>
                                </h3>
                            </div>

                            <div className=' col-6 ms-5' style={{ height: "15em", overflow: "auto" }}>
                                {
                                    studentData === 0 ? <></> :
                                        <>
                                            <h5 className='mb-3 float-start'><b><i><i class="bi bi-chevron-double-left"></i> Top Scorer <i class="bi bi-chevron-double-right"></i></i></b></h5>
                                            <table className="table table-bordered"  >
                                                <thead>
                                                    <tr className='table'>
                                                        <th scope="col" style={{ width: "2em" }}>Sr.No</th>
                                                        <th scope="col">PRN</th>
                                                        <th scope="col">Student Name</th>
                                                        <th scope="col">Overall Score</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {
                                                        studentData.filter((data) => parseInt(data.studentId) !== 0).sort((a, b) => { return (b.overallPercentage - a.overallPercentage) }).map((data, i = 1) => {
                                                            return (
                                                                <tr key={data.studentId}>
                                                                    <th scope="row">{++i}</th>
                                                                    <td>{data.studentPrn}</td>
                                                                    <td>{data.studentName}</td>
                                                                    <td>{data.overallPercentage}</td>
                                                                </tr>
                                                            )
                                                        })
                                                    }
                                                </tbody>
                                            </table>
                                        </>
                                }
                            </div>

                        </div>
                    </div>
                </div >
            </div>
        </div >
    )
}
