import { Link, useNavigate } from 'react-router-dom'
import axios from 'axios';
import { url } from '../api/URLbackend';
import toast from 'react-hot-toast';

const NewLogin = () => {
    let navigate = useNavigate();  

    const handleSubmit = (e) => {
        e.preventDefault();

        const user = {
            username: e.target.username.value,
            password: e.target.password.value
        }

        axios.post(url + `login`, null, { params: { username: user.username, password: user.password } })
            .then((res) => {
                if (res.data.role === "teacher") {
                    localStorage.setItem("teacherId", res.data.tId.teacherId)
                    if( res.data.tId.yrOfExp === null ){
                        localStorage.setItem("yrOfExp", 0)
                    }else{
                        localStorage.setItem("yrOfExp", res.data.tId.yrOfExp)
                    }
                    localStorage.setItem("teacherName", res.data.tId.teacherName)
                    navigate('/DashBoardTeacher')

                } else if (res.data.role === "student") {
                    localStorage.setItem("studentId", res.data.sId.studentId)
                    localStorage.setItem("studentName", res.data.sId.studentName)
                    navigate('/DashBoardStudent')
                } else {
                    toast.error("Invalid pass /Username");
                }
            })
            .catch(function (error) {
                toast.error("Invalid pass /Username");
            })
    }

    return (
        <div className="register d-flex justify-content-center align-items-center" style={{ backgroundColor: "darkslateblue", width: "106em", height: "48em" }}>
            <div className="register d-flex justify-content-around text-light" style={{ width: "51em" }} >
                <div className="d-flex flex-column" style={{ width: "23em", height: "30em", backgroundColor: "#6c62ab", borderTopLeftRadius: "8em", borderBottomRightRadius: "8em", boxShadow: "-15px 16px" }}>
                    <h2 className='mt-5'><i>Login : </i></h2>

                    <form onSubmit={handleSubmit}>
                        <div className='d-flex flex-column mt-5' style={{}}>
                            <input className='mt-5 ms-5' type="text" name="username" placeholder='username' style={{ width: "17em", height: "38px", borderRadius: "11px" }} required />
                            <input className='mt-5 ms-5' type="text" name="password" placeholder='password' style={{ width: "17em", height: "38px", borderRadius: "11px" }} required />

                            <button className='col-4  btn mt-5' style={{ marginLeft: "7em", backgroundColor: "#bba5de", fontWeight: "bold" }}> LOGIN </button>
                        </div>
                    </form>

                </div>
                <div >
                    <p className='mt-3'>
                        <i style={{ position: "relative", top: "5em", left: "18em" }}>Dont have an account ?<Link to="/RegisterUser" ><u> Register </u></Link></i>
                    </p>
                </div>
                <div className="col-2">
                    <img src="images/bglogin.svg" alt="" style={{ position: "relative", top: "9em" }} />
                </div>
                    
            </div>
        </div>
    )
}

export default NewLogin