import axios from 'axios'
import { useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import { url } from '../api/URLbackend'
import toast from 'react-hot-toast'

const RegisterUser = () => {

    let navigate = useNavigate();

    const style = {
        width: "17em", height: "38px",
        borderRadius: "11px"
    }

    const [name, setName] = useState()
    const [email, setEmail] = useState()
    const [username, setUsername] = useState()
    const [password, setPassword] = useState()
    const [phone, setPhone] = useState()
    const [role, setRole] = useState("student")

    const sentDataToUser = (user) => {
        if (user === username) {
            document.getElementById('username').value = '';
            toast.error("UserName already present !! Try Again ");
            return;
        }

        let sUser = {
            studentName: name,
            studentMail: email
        }


        if (user !== username) {
            const sendDataToUser = async (temp) => {
                await axios.post(url + `register`, temp)
                    .then(() => {
                        toast.success("Registeration success")
                        navigate('/')
                    })
                    .catch(function (error) {
                        toast.error("Something Wents Wrong !");
                    })
            }
            const sendDataToPersonalUser = async () => {
                if (role === "teacher") {

                    await axios.post(url + `addteacher`, null, { params: { name: sUser.studentName, email: sUser.studentMail } })
                        .then((res) => {
                            let temp = {
                                username: username,
                                password: password,
                                phone: phone,
                                role: role,
                                tId: {
                                    teacherId: res.data
                                }
                            }
                            sendDataToUser(temp);
                        })
                        .catch(function (error) { toast.error("Something Wents Wrong !"); })
                } else {
                    await axios.post(url + `addstudent`, null, { params: { name: sUser.studentName, email: sUser.studentMail } })
                        .then((res) => {
                            let temp = {
                                username: username,
                                password: password,
                                phone: phone,
                                role: role,
                                tId: {
                                    teacherId: 0
                                },
                                sId: {
                                    studentId: res.data
                                }
                            }
                            sendDataToUser(temp);
                        })
                        .catch(function (error) { toast.error("Something Wents Wrong !"); })
                }
            }
            sendDataToPersonalUser();
        }

    }

    const sendDataToAPI = () => {

        var a = document.getElementById("pass1").value;
        var b = document.getElementById("pass2").value;

        if (a !== b) {
            toast.error("password mismatch");
            return;
        }

        axios.post(url + `find`, null, { params: { username: username } })
            .then((res) => {
                sentDataToUser(res.data)
            });
    }

    return (
        <div className="register d-flex justify-content-center align-items-center" style={{ backgroundColor: "darkslateblue", width: "106em", height: "48em" }}>
            <div className="register d-flex justify-content-around text-light" style={{ width: "51em" }} >
                <div className="d-flex flex-column" style={{ width: "23em", height: "35em", backgroundColor: "#6c62ab", borderTopLeftRadius: "8em", borderBottomRightRadius: "8em", boxShadow: "-15px 16px" }}>
                    <h2 className='mt-5'><i>Register here : </i></h2>

                    <div className='d-flex flex-column mt-5' style={{}}>
                        <input style={style} className=' ms-5' type="text" name="name" placeholder='Your Name' onChange={(e) => setName(e.target.value)} required />
                        <input style={style} className=' ms-5 mt-3' type="text" name="email" placeholder='Email ' onChange={(e) => setEmail(e.target.value)} required />
                        <input style={style} className=' ms-5  mt-3' type="text" name="username" id="username" placeholder='username' onChange={(e) => setUsername(e.target.value)} required />
                        <input style={style} className='mt-3 ms-5' type="password" id='pass1' name="password" placeholder='password' onChange={(e) => setPassword(e.target.value)} required />
                        <input style={style} className='mt-3 ms-5' type="password" id='pass2' name="password1" placeholder='confirm password' required /><br />
                        <input style={style} className='ms-5' type="text" name="phno" placeholder='mobile number' onChange={(e) => setPhone(e.target.value)} required /><br /><br />
                        <div className='d-flex col-9 justify-content-end ' style={{ position: "relative", top: "-1em" }}>
                            <select className=' form-select' name="role" aria-label="Default select example" style={{ height: "3em", width: "8em", marginLeft: "7em" }} onChange={(e) => setRole(e.target.value)}>
                                <option value="student" >Student</option>
                                <option value="teacher" >Teacher</option>
                            </select>
                            <button className='col-4 ms-5 btn' style={{ backgroundColor: "#bba5de", fontWeight: "bold" }} onClick={() => sendDataToAPI()}> Register  </button>
                        </div>

                    </div>

                </div>
                <Link to="/">
                    <u>
                        <p style={{ position: "relative", top: "7em", left: "16em" }}>Go to Login </p>
                    </u>
                </Link>
                <div className="col-2">
                    <img src="images/bglogin.svg" alt="" style={{ position: "relative", top: "9em" }} />
                </div>
            </div>
        </div>
    )
}

export default RegisterUser;

