import '../App.css';
import React from 'react'
import { Link, useNavigate } from 'react-router-dom'

const NavbarStudent = () => {

    let navigate = useNavigate();
    const handleLogOut = () => {
        localStorage.clear();
        navigate("/")
    }

    return (
        <div>
            <div className="d-inline" style={{ backgroundColor: "red", width: "18em" }}>
                <div className="logo d-flex flex-row">
                    <img src="/images/logo.png" alt='logo' className="commonlogo" />
                    <div className="orgname text-light d-flex justify-content-center align-items-center flex-column">
                        <h4> OERM </h4>
                    </div>
                </div>
                <div className="navvbar text-light d-flex flex-column justify-content-center ">
                    <div className="eachBox"> <Link to="/DashBoardStudent" > DashBoard    </Link></div>
                    <div className="eachBox"> <Link to="/CoursesStudent"   > Courses      </Link></div>
                    <div className="eachBox"> <Link to="/StudentProfile"   > Profile      </Link></div>
                    <div className="eachBox"> <Link to="/GradeCardStudent" > Grade Cards  </Link></div>
                    <div className="eachBox"> <Link to="/Expertise"        > Expertise    </Link></div>
                    <div className="eachBox"> <Link to="/StudentHelp"      > Help         </Link></div>
                    <div className="eachBox"> <Link to="/AboutStudent"     > About       </Link></div>

                    <div className="eachBox btnn">
                        <Link to="/">
                            <button type="submit" className="eachBtn" form="form1" value="logOut" onClick={handleLogOut}><i
                                className="bi bi-box-arrow-right"></i>&nbsp;Log-Out
                            </button>
                        </Link>
                    </div>

                </div>
            </div>
        </div >
    )
}

export default NavbarStudent;