import axios from 'axios';
import { Row, Col, Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import React, { useState, useEffect } from 'react'
import { Link} from 'react-router-dom';
import { url } from '../api/URLbackend';
import NavbarTeacher from '../components/NavbarTeacher';
import toast from 'react-hot-toast';

const Student = () => {

    const [attendance, setAttendance] = useState(true)
    const [getAttendance, setGetAttendance] = useState()
    const [assign, setAssign] = useState()
    const [courselink, setCourselink] = useState()
    const [manage, setManage] = useState()

    const [assignNo, setAssignNo] = useState()
    const [subjectName, setSubjectName] = useState()
    const [startDate, setStartDate] = useState()
    const [endDate, setEndDate] = useState()
    const [question, setQuestion] = useState()

    const [courseSubjectName, setCourseSubjectName] = useState()
    const [subjectLink, setSubjectLink] = useState()
    
    const [studentData, setStudentData] = useState(0)
    const [assignment, setAssignment] = useState(0)
    const [notesLink, setNotesLink] = useState(0)
    const [allAttendance, setAllAttendance] = useState(0)

    const [listOfPresent, setListOfPresent] = useState([])
    const [attendDate, setAttendDate] = useState(' ')
    const [isChecked, setIsChecked] = useState(false)
    const [checkDate, setCheckDate] = useState(0)
    const [getAtten, setGetAtten] = useState(false)
    
    useEffect(() => {
        axios.post(url + `showallstudent`)
            .then((res) => {
                setStudentData(res.data);
            })

        axios.get(url + `showallAssign`)
            .then((res) => {
                setAssignment(res.data);
            })

        axios.get(url + `showallnoteslink`)
            .then((res) => {
                setNotesLink(res.data)
            })
            .catch((err) => {
                toast.error("Something wents wrong")
            })
        // 
        axios.get(url + `showallattendance`)
            .then((res) => {
                setAllAttendance(res.data)
            })
            .catch((err) => {
                toast.error("Something wents wrong")
            })

    }, [])


    const showAttendance = () => {
        setAttendance(!false)
        setGetAttendance(false)
        setAssign(false)
        setCourselink(false)
        setManage(false)
    }

    const showGetAttendance = () => {
        setAttendance(false)
        setGetAttendance(!false)
        setAssign(false)
        setCourselink(false)
        setManage(false)
    }

    const showAssign = () => {
        setAttendance(false)
        setGetAttendance(false)
        setAssign(!false)
        setCourselink(false)
        setManage(false)
    }

    const showcourselink = () => {
        setAttendance(false)
        setGetAttendance(false)
        setAssign(false)
        setCourselink(!false)
        setManage(false)
    }

    const showManage = () => {
        setAttendance(false)
        setGetAttendance(false)
        setAssign(false)
        setCourselink(false)
        setManage(!false)
    }
    
    const [modal, setModal] = useState(false);
    const toggle = () => setModal(!modal);

    const handleAddAssign = () => {

        for (let i = 0; i < studentData.length; i++) {

            let temps = {
                assignNo: assignNo,
                subjectName: subjectName,
                startDate: startDate,
                endDate: endDate,
                question: question,
                sid: {
                    studentId: studentData[i].studentId
                }
            }

            axios.post(url + `addassign`, temps)
                .then((res) => {
                    toast.success("Assignment Added >>")
                })
                .catch(function (error) { toast.error("Something wents wrong !") })
        }
    }

    const handleAddLink = () => {

        let temp = {
            subName: courseSubjectName,
            subLink: subjectLink
        }

        axios.post(url + `addnoteslink`, temp)
            .then((res) => {
                toast.success("Link Added >>")
            })
            .catch(function (error) { toast.error("Something wents Wrong !") })
    }

    const deleteStudentBySid = (tempSid) => {

        axios.get(url + `deletestudent`, { params: { sid: tempSid } })
            .then((res) => {
                setStudentData(res.data);
                toast.success("Student deleted >>")
            })
    }

    const deleteByUserId = (tempId, studId) => {

        console.log("userId : " + tempId)

        axios.get(url + `deletestudentbyuserId`, { params: { userId: tempId } })
            .then((res) => {
                deleteStudentBySid(studId);
            })
    }

    const deleteStudent = (studId) => {

        axios.get(url + `getstudentbysid`, { params: { sid: studId } })
        .then((res) => {
                deleteByUserId(res.data, studId)
            })
    }

    
    const handleAttendance = (prns, studname) => {
        if (attendDate === ' ') {
            toast.error("Date is not selected !! ")
            let ele = document.getElementsByName('attend')

            for (let i = 0; i < ele.length; i++) {
                if (ele[i].type === 'checkbox') {
                    ele[i].checked = false;
                }
            }
            return;
        }
        setIsChecked(true);

        var temp = {
            currDate: attendDate,
            prn: prns,
            studentName: studname
        }
        listOfPresent.push(temp)
    }

    const sendAttendanceTODB = () => {

        var call = 0;

        for (let i = 0; i < listOfPresent.length; i++) {

            var temp = listOfPresent[i]

            axios.post(url + `addattendance`, temp)
                .then((res) => {
                })
                .catch(function (error) { toast.error("Something wents wrong ! ") })

            if (i === listOfPresent.length - 1) {
                call = 1;
            }
        }
        if (call === 1) {
            toast.success("Attendance Saved >>")
            setListOfPresent(0)
            setTimeout(() => {
                let ele = document.getElementsByName('attend')

                for (let i = 0; i < ele.length; i++) {
                    if (ele[i].type === 'checkbox') {
                        ele[i].checked = false;
                    }
                }
            }, 2000);
        }
    }

    const handleAddMarks = (sId, aNo, mark) => {

        axios.post(url + `addmarkstoeachstudent`, null, { params: { sid: sId, assignno: aNo, marks: mark } })
            .then((res) => {
                setAssignment(res.data);
                toast.success(mark + " Marks allocated")
            })
            .catch(function (error) { toast.error("Something Wents Wrong !"); })
    }

    return (
        <div>
            <div>
                <div className="cont container-fluid p-3 d-flex justify-content-center align-items-center">
                    <div className="row" style={{ backgroundColor: "#1f153c", width: "21em" }}>
                        <NavbarTeacher />
                    </div>
                    <div className="main col-9 d-flex align-items-center flex-column">
                        <div className="upp ">
                        </div>
                        <div className="low ">
                            <div className='mt-4 d-flex ' style={{ height: "36px" }} >
                                <button className='ms-5 btn btn-primary d-flex justify-content-center' style={{ width: "10em" }} onClick={() => showAttendance()}   > Add Attendance  </button>
                                <button className='ms-5 btn btn-primary d-flex justify-content-center' style={{ width: "10em" }} onClick={() => showGetAttendance()}   > Get Attendance  </button>
                                <button className='ms-5 btn btn-success d-flex justify-content-center ' style={{ width: "8em" }} onClick={() => showAssign()}   > Assignments  </button>
                                <button className='ms-5 btn btn-info d-flex justify-content-center    text-light' style={{ width: "8em" }} onClick={() => showcourselink()}   > Notes Link  </button>
                                <button className='ms-5 btn btn-warning d-flex justify-content-center text-light' style={{ width: "8em" }} onClick={() => showManage()}   > Manage </button>
                            </div>
                            <hr />

                            {
                                studentData === 0 ? <></> :

                                    !attendance ? <></> :
                                        <>
                                            <div className='atttdiv'>
                                                <div className='mt-4 d-flex' style={{ height: "20em" }}>
                                                    <div className='col-6 ' style={{ marginLeft: "7em" }}>
                                                        <h4 className='mt-3 float-start'><u>Students List</u></h4>
                                                        <input className='btn btn-outline-primary mb-5 mt-3' type="date" style={{ marginLeft: "21em" }} value={attendDate} onChange={(e) => setAttendDate(e.target.value)} />
                                                        <div className='' style={{ height: "18em", overflowY: "auto" }}>
                                                            <table className="table table-bordered"  >
                                                                <thead>
                                                                    <tr className='table'>
                                                                        <th scope="col" style={{ width: "2em" }}>Sr.No</th>
                                                                        <th scope="col">PRN</th>
                                                                        <th scope="col">Student Name</th>
                                                                        <th scope="col" style={{ width: "2em" }}>Presents</th>
                                                                    </tr>
                                                                </thead>
                                                                <tbody>
                                                                    {
                                                                        studentData.filter((data) => parseInt(data.studentId) !== 0).map((data, i = 1) => {
                                                                            return (
                                                                                <tr key={data.studentId}>
                                                                                    <th scope="row">{++i}</th>
                                                                                    <td>{data.studentPrn}</td>
                                                                                    <td>{data.studentName}</td>
                                                                                    <td><input className='form-check-input mt-2' type="checkbox" name="attend" value={isChecked} onChange={() => handleAttendance(`${data.studentPrn}`, `${data.studentName}`)} /></td>
                                                                                </tr>
                                                                            )
                                                                        })
                                                                    }
                                                                </tbody>
                                                            </table>
                                                        </div>
                                                    </div>

                                                </div>
                                            </div>
                                            <button className="btn btn-primary col-1" style={{ position: "relative", left: "-4em", top: "8em" }} onClick={() => sendAttendanceTODB()}> Submit </button>
                                        </>
                            }

                            {
                                !getAttendance ? <></> :
                                    <>
                                        <div className='d-flex'>
                                            <div className='mt-5 ms-5 col-4' >
                                                <h4>Check Attendance</h4>
                                                <div className='d-flex flex-column mt-5 float-center' style={{ marginLeft: "6em" }}>
                                                    <input className='btn btn-outline-primary mt-3 col-8' type="date" value={checkDate} onChange={(e) => setCheckDate(e.target.value)} />
                                                    <button className='btn btn-primary col-8 mt-3' onClick={() => setGetAtten(true)}>Get Attendance</button>
                                                </div>
                                            </div>
                                            <div className='col-6 ms-4 mt-5' style={{ height: "28em", overflow: "auto" }}>
                                                {
                                                    allAttendance === 0 ? <></> :
                                                        !getAtten ? <>Please select date</> :
                                                            <table className=" table table-bordered">
                                                                <thead>
                                                                    <tr>
                                                                        <th style={{ width: "3em", color: "black" }} scope="col">Sr.No </th>
                                                                        <th style={{ width: "15em", color: "black" }} scope="col">Date </th>
                                                                        <th style={{ width: "15em", color: "black" }} scope="col">PRN </th>
                                                                        <th style={{ width: "15em", color: "black" }} scope="col">Student Name </th>
                                                                    </tr>
                                                                </thead>
                                                                <tbody >
                                                                    {
                                                                        allAttendance.filter((data) => checkDate === data.currDate).map((data, i = 0) => {
                                                                            return (
                                                                                <tr key={data.prn}>
                                                                                    <th scope="row" style={{ color: "black" }}>{++i}</th>
                                                                                    <td style={{ color: "black" }}>{data.currDate}</td>
                                                                                    <td style={{ color: "black" }}>{data.prn}</td>
                                                                                    <td style={{ color: "black" }}>{data.studentName}</td>
                                                                                </tr>
                                                                            )
                                                                        })

                                                                    }

                                                                </tbody>
                                                            </table>
                                                }
                                            </div>
                                        </div>
                                    </>
                            }

                            {
                                !assign ? <></> :
                                    <div className='mt-4 bg-white' style={{ height: "28em" }}>
                                        <div className='mt-4 d-flex' style={{ height: "36px" }} >
                                            <div className="form-group ms-5">
                                                <label htmlFor="" className="fw-bold" >Assignment No. : </label>
                                                <input type="text" className="form-control" value={assignNo} onChange={(e) => setAssignNo(e.target.value)} />
                                            </div>
                                            <div className="form-group ms-4">
                                                <label htmlFor="" className="fw-bold">Subject Name: </label>
                                                <input type="text" className="form-control" value={subjectName} onChange={(e) => setSubjectName(e.target.value)} />
                                            </div>
                                            <div className="form-group ms-5">
                                                <label htmlFor="" className="fw-bold">Start date : </label>
                                                <input type="date" className="form-control" value={startDate} onChange={(e) => setStartDate(e.target.value)} />
                                            </div>
                                            <div className="form-group ms-4">
                                                <label htmlFor="" className="fw-bold">End date : </label>
                                                <input type="date" className="form-control" value={endDate} onChange={(e) => setEndDate(e.target.value)} />
                                            </div>

                                        </div>
                                        <div className='d-flex' style={{ marginTop: "4em" }}>
                                            <div className="mt-5 ms-5 col-7 d-flex flex-column " style={{ height: "23em" }}>
                                                <p className="mt-4 d-flex flex-start">List of Questions : </p>
                                                <textarea rows="18" name="comment" form="usrform" style={{ width: "31em" }} value={question} onChange={(e) => setQuestion(e.target.value)} />
                                                <button type="submit" onClick={() => handleAddAssign()} className="btn btn-outline-primary" style={{ width: "99px", position: "relative", left: "32em", top: "-44px" }}> Send </button>
                                            </div>
                                            <div className='mt-5 col-4 ' style={{ height: "23em" }}>
                                                <Button className='btn-outline-primary col-9 mt-4 text-dark' style={{ backgroundColor: "white" }} onClick={toggle}>Show All Assignments</Button>
                                                <Modal isOpen={modal} toggle={toggle} >
                                                    <div className='modal-content ' style={{ width: "57em" }}>
                                                        <ModalHeader className='ms-5'><span style={{ color: "black", fontWeight: "bold" }}> Assignment Questions </span></ModalHeader>
                                                        <ModalBody className='p-3'>
                                                            <table className="table border" >
                                                                <thead>
                                                                    <tr className='table-primary fw-normal'>
                                                                        <th style={{ width: "4em", color: "black" }} scope="col">Assign. No</th>
                                                                        <th style={{ width: "11em", color: "black" }} scope="col">Students PRN</th>
                                                                        <th style={{ width: "11em", color: "black" }} scope="col">Students Name</th>
                                                                        <th style={{ width: "11em", color: "black" }} scope="col">Subject name</th>
                                                                        <th style={{ width: "30em", color: "black" }} scope="col">Allocated Marks</th>
                                                                        <th style={{ width: "30em", color: "black" }} scope="col">Out of Marks</th>
                                                                        <th style={{ width: "30em", color: "black" }} scope="col">Changed Marks</th>
                                                                    </tr>
                                                                </thead>
                                                                <tbody >
                                                                    {

                                                                        assignment.sort((a, b) => { return (a.assignNo - b.assignNo); }).filter((data) => parseInt(data.sid.studentId) !== 0).map((data ) => {
                                                                            return (
                                                                                <tr key={data.assignId}>
                                                                                    <th style={{ color: "black" }} scope="row"> {data.assignNo}</th>
                                                                                    <td style={{ color: "black", wordBreak: "break-word" }}>{data.sid.studentPrn}</td>
                                                                                    <th style={{ color: "black" }} scope="row">{data.sid.studentName} </th>
                                                                                    <td>{data.subjectName}</td>
                                                                                    <td>{data.marks}</td>
                                                                                    <td>20</td>
                                                                                    <td className='d-flex '>
                                                                                        <input className='ms-5' type='text' placeholder='Marks' id="marksInp" max={20} onBlur={(e) => handleAddMarks( `${data.sid.studentId}`, `${data.assignNo}` ,e.target.value)} />
                                                                                    </td>
                                                                                </tr>
                                                                            )
                                                                        })
                                                                    }
                                                                </tbody>
                                                            </table>
                                                        </ModalBody>
                                                        <ModalFooter>
                                                            <Row style={{ height: "3em" }}>
                                                                <Col>
                                                                    <Button color="primary" onClick={toggle}>Okay! </Button>
                                                                </Col>
                                                                <Col>
                                                                    <Button color="secondary" onClick={toggle}>Cancel</Button>
                                                                </Col>
                                                            </Row>
                                                        </ModalFooter>
                                                    </div>
                                                </Modal>
                                            </div>
                                        </div>
                                    </div>

                            }

                            {
                                !courselink ? <></> :
                                    <div className='mt-5 d-flex flex-column' style={{ height: "10em" }}>
                                        <div className='d-flex'>
                                            <div className="mt-4 form-group ms-4 col-3">
                                                <label htmlFor="" className="fw-bold">Subject Name: </label>
                                                <input type="text" className="form-control" value={courseSubjectName} onChange={(e) => setCourseSubjectName(e.target.value)} />
                                            </div>
                                            <div className="mt-4 form-group col-7 ms-4">
                                                <label htmlFor="" className="fw-bold">Paste your Drive link here : </label>
                                                <input type="text" className="form-control" value={subjectLink} onChange={(e) => setSubjectLink(e.target.value)} />
                                            </div>
                                            <Button className='btn-outline-primary col-1 text-dark mt-5 ms-4' onClick={() => handleAddLink()} style={{ height: "2em", backgroundColor: "white" }}>Add</Button>
                                        </div>
                                        <div className='mt-5 col-8 ' style={{ height: "19em", marginLeft: "12em", overflow: "auto" }}>
                                            {
                                                notesLink === 0 ? <></> :
                                                    <table className=" table table-bordered">
                                                        <thead>
                                                            <tr>
                                                                <th style={{ width: "3em", color: "black" }} scope="col">Sr.No </th>
                                                                <th style={{ width: "15em", color: "black" }} scope="col">Subject </th>
                                                                <th scope="col">
                                                                    <span style={{ color: "black" }}>Link to Drive</span>
                                                                </th>
                                                            </tr>
                                                        </thead>
                                                        <tbody >

                                                            {
                                                                notesLink.map((data) => {
                                                                    return (
                                                                        <tr key={data.subId}>
                                                                            <th scope="row" style={{ color: "black" }}>{data.subId}</th>
                                                                            <td style={{ color: "black" }}>{data.subName}</td>
                                                                            <td>
                                                                                <p style={{ color: "blue" }} >{data.subLink}</p>
                                                                            </td>
                                                                        </tr>
                                                                    )
                                                                })
                                                            }

                                                        </tbody>
                                                    </table>
                                            }
                                        </div>
                                    </div>

                            }

                            {
                                studentData === 0 ? <></> :

                                    !manage ? <></> :
                                        <div className='bg-white d-flex align-items-center' style={{ height: "33em" }}>
                                            <div className='col-8' style={{ marginLeft: "7em" }}>
                                                <div className='d-flex justify-content-between '>
                                                    <h5 className='mt-2'><b>Students List</b></h5>
                                                </div>
                                                <div className=' mt-5' style={{ overflowY: "auto", height: "23em" }}>
                                                    <table className="table table-bordered">
                                                        <thead>
                                                            <tr className='table'>
                                                                <th scope="col" style={{ width: "2em" }}>Sr.No</th>
                                                                <th scope="col">PRN</th>
                                                                <th scope="col">Student Name</th>
                                                                <th scope="col">Details/Update</th>
                                                                <th scope="col">Delete</th>
                                                            </tr>
                                                        </thead>
                                                        <tbody>

                                                            {
                                                                studentData.filter((data) => parseInt(data.studentId) !== 0).map((data, i = 1) => {
                                                                    return (

                                                                        <tr key={data.studentId}>
                                                                            <th scope="row">{++i}</th>
                                                                            <td>{data.studentPrn}</td>
                                                                            <td>{data.studentName}</td>
                                                                            <td>
                                                                                <Link to='/StudentData' state={{
                                                                                    Id: `${data.studentId}`,
                                                                                    Name: `${data.studentName}`,
                                                                                    Prn: `${data.studentPrn}`,
                                                                                    Address: `${data.studentAddress}`,
                                                                                    Course: `${data.studentCourse}`,
                                                                                    Dob: `${data.studentDob}`,
                                                                                    Gender: `${data.studentGender}`,
                                                                                    Mail: `${data.studentMail}`,
                                                                                    Phone: `${data.studentPhone}`

                                                                                }} className='text-primary' >
                                                                                    <u>Get/Set Details</u>
                                                                                </Link>
                                                                            </td>
                                                                            <td><h4 ><i class="bi bi-trash3 text-danger" onClick={() => deleteStudent(data.studentId)}></i></h4></td>
                                                                        </tr>

                                                                    )
                                                                })
                                                            }
                                                        </tbody>
                                                    </table>
                                                </div>
                                            </div>
                                            <div style={{ position: "relative", top: "-13em", left: "3em" }}>
                                                <abbr title='Add student'>
                                                    <Link to="/AddStudent">
                                                        <button className='btn btn-outline-primary' style={{ height: "3em", borderRadius: "50%", position: "relative", left: "2em", width: "3em" }}><h1><i class="bi bi-plus-circle" style={{ position: "relative", top: "-7px", left: "-9px" }}></i></h1></button>
                                                    </Link>
                                                </abbr>
                                            </div>
                                        </div>
                            }
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Student;