import './App.css';
import AboutTeacher from './Teacher/AboutTeacher';
import TeacherProfile from './Teacher/TeacherProfile';
import DashBoardTeacher from './Teacher/DashBoardTeacher';
import Announcement from './Teacher/Announcement';
import ExpertiseT from './Teacher/ExpertiseT';

import StudentProfile from './Student/StudentProfile';
import AboutStudent from './Student/AboutStudent';
import DashBoardStudent from './Student/DashBoardStudent';
import Expertise from './Student/Expertise';
import StudentHelp from './Student/StudentHelp';
import CoursesStudent from './Student/CoursesStudent';
import GradeCardStudent from './Student/GradeCardStudent';

import NewLogin from './pages/NewLogin';
import RegisterUser from './pages/RegisterUser';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import { Provider } from 'react-redux';
import store from './store/store';
import Student from './Teacher/Student';
import StudentData from './Teacher/StudentData';
import AddStudent from './Teacher/AddStudent';
import Exception from './pages/Exception';

import {Toaster} from 'react-hot-toast'

function App() {

  return (
    <>
      <div>
        <Toaster position="top-right"></Toaster>
      </div>
      <Provider store={store} >
        <BrowserRouter>
          <Routes>
            {/* <Switch> */}

            <Route exact path="/" element={<NewLogin />}  ></Route>
            <Route exact path="/RegisterUser" element={<RegisterUser />}  ></Route>

            {/*   student   */}
            <Route exact path="/DashBoardStudent" element={<DashBoardStudent />} ></Route>
            <Route exact path="/CoursesStudent" element={<CoursesStudent />} ></Route>
            <Route exact path="/StudentProfile" element={<StudentProfile />} ></Route>
            <Route exact path="/GradeCardStudent" element={<GradeCardStudent />} ></Route>
            <Route exact path="/Expertise" element={<Expertise />} ></Route>
            <Route exact path="/AboutStudent" element={<AboutStudent />} ></Route>
            <Route exact path="/StudentHelp" element={<StudentHelp />} ></Route>

            {/*   Teacher   */}
            <Route exact path="/DashBoardTeacher" element={<DashBoardTeacher />} ></Route>
            <Route exact path="/Student" element={<Student />} ></Route>
            <Route exact path="/StudentData" element={<StudentData />} ></Route>
            <Route exact path="/AddStudent" element={<AddStudent />} ></Route>
            <Route exact path="/ExpertiseT" element={<ExpertiseT />} ></Route>
            <Route exact path="/AboutTeacher" element={<AboutTeacher />} ></Route>
            <Route exact path="/Announcement" element={<Announcement />} ></Route>
            <Route exact path="/TeacherProfile" element={<TeacherProfile />} ></Route>

            <Route exact path="*" element={<Exception />} ></Route>
            <Route exact path="/error" element={<Exception />} ></Route>

            {/* </Switch>   switch for exact match */}
          </Routes>
        </BrowserRouter>
      </Provider>
    </>
  );
}
export default App;