import React from 'react'
import NavbarTeacher from '../components/NavbarTeacher';
import { useRef } from 'react';
import { useDispatch } from 'react-redux';
import { Announce } from '../store/AnnounceSlice';

const Announcement = () => {

    const ref = useRef(null);
    const dispatch = useDispatch();

    const getAnnouncee = ()=>{
        var com ={ AnnounceToS : ref.current.value }
        dispatch(Announce(com))
    }

    return (
        <div>
            <div className="cont container-fluid p-3 d-flex justify-content-center align-items-center">
                <div className="row" style={{  backgroundColor: "#1f153c" , width: "21em"}}>
                     <NavbarTeacher />
                </div>
                <div className="main col-9 d-flex align-items-center flex-column">
                <div className="upp ">
                </div>
                <div className="low ">
                    <div className="help">
                        <h3 className="text-center mt-3">Announcement For Student &nbsp;<i className="bi bi-megaphone"></i></h3>
                    </div>
                    <div className="helpbox mt5">
                        <textarea rows="9" cols="70" name="comment" form="usrform" ref={ref}   /> 
                        <p className="mb-1">Write your message here : </p>
                        <div>
                            <button type="submit"  className="helpbtn btn btn-outline-primary" onClick={()=> getAnnouncee() }> Send </button>
                        </div>
                    </div>
                </div>
            </div>
            </div>
            
        </div>
    )
}

export default Announcement;