import axios from 'axios'
import React, { useState } from 'react'
import { useEffect } from 'react'
import toast from 'react-hot-toast'
import { url } from '../api/URLbackend'
import NavbarStudent from '../components/NavbarStudent'

const GradeCardStudent = () => {

    const [assignment, setAssignment] = useState()
    const [assignmentData, setAssignmentData] = useState(0)

    useEffect(() => {
        axios.post(url + `geteachstudentmarks`, null, { params: { sid: localStorage.getItem("studentId") } })
            .then((res) => {
                let data = res.data
                setAssignmentData(data)
            })
            .catch(function (error) {
                toast.error("Something wents wrong !");
            })

    }, [])



    return (
        <div>
            <div className="cont container-fluid p-3 d-flex justify-content-center align-items-center">
                <div className="row" style={{ backgroundColor: "#1f153c", width: "21em" }}>
                    <NavbarStudent />
                </div>
                <div class="main col-9 d-flex align-items-center flex-column">
                    <div class="upp ">

                    </div>
                    <div class=" low ">
                        <h2 class="d-flex justify-content-center mt-2">Grade Cards &nbsp;<i class="bi bi-trophy-fill text-primary"></i></h2>
                        <div class="col-12 d-flex" style={{ position: "relative", top: "9em", height: "24em" }}>
                            <div class="card text-white  d-flex justify-content-center align-items-center flex-column " style={{ width: "18em", height: "20em", border: "none" }}>
                                <div class="mb-5 btn btn-outline-primary card-header d-flex justify-content-center align-items-center fw-bold " style={{ height: "8em", width: "14em", borderRadius: "1rem" }} onClick={() => setAssignment(!false)} >Marks of Assignment</div>
                            </div>
                            <div className='ms-5' style={{ width: '45em' }}>

                                {/* subname , startdate , enddate , score  */}

                                {
                                    assignmentData === 0 ? <></> :
                                        !assignment ? <></> :

                                            <table className="table table-bordered">
                                                <thead>
                                                    <tr>
                                                        <th style={{ width: "2em" }} scope="col">Assignment No.</th>
                                                        <th scope="col">Subject</th>
                                                        <th scope="col">Start Date</th>
                                                        <th scope="col">End Date</th>
                                                        <th style={{ width: "3em" }} scope="col">Score</th>
                                                    </tr>
                                                </thead>
                                                <tbody >

                                                    {
                                                        assignmentData.sort((a, b) => { return (a.assignNo - b.assignNo); }).map((data) => {
                                                            return (
                                                                <tr key={data.assignId}>
                                                                    <th scope="row">{data.assignNo}</th>
                                                                    <td>{data.subjectName}</td>
                                                                    <td>{data.startDate}</td>
                                                                    <td>{data.endDate}</td>
                                                                    <td>{data.marks}</td>
                                                                </tr>
                                                            )
                                                        })
                                                    }

                                                </tbody>
                                            </table>
                                }
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default GradeCardStudent