import React from 'react'
import { useDispatch} from 'react-redux';
import NavbarStudent from '../components/NavbarStudent';
import { helps } from '../store/helpSlice';
import { useRef } from 'react'

const StudentHelp = () => {
    
    const ref = useRef(null);
    const dispatch = useDispatch();

    
    const sendHelp = ()=>{
        var com ={ helpMessage : ref.current.value }
        dispatch(helps(com))
    }
    
    return (
        <div>
            <div className="cont container-fluid p-3 d-flex justify-content-center align-items-center">
                <div className="row" style={{ backgroundColor: "#1f153c", width: "21em" }}>
                    <NavbarStudent />
                </div>
                <div className="main col-9 d-flex align-items-center flex-column">
                    <div className="upp ">
                    </div>
                    <div className="low ">
                        <div className="help">
                            <h3 className=" text-center mt-3">Help <i className="bi bi-question-square text-primary"></i></h3>
                        </div>
                        <div className="helpbox mt5">
                            <textarea rows="9" cols="70" name="comment" ref={ref} form="usrform" />
                            <p className="mb-1">Share your problem here : </p>
                            <div>
                                <button type="submit" className="helpbtn btn btn-outline-primary" onClick={() => sendHelp()}> Send </button>
                            </div>
                        </div>
                        <pre
                            className="text-center">Copyright &copy; by CDAC New Delhi 2022 Terms and condition applied All Right Reserved .</pre>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default StudentHelp;