import React from 'react'
import { Link } from 'react-router-dom'

const Exception = () => {
  return (
    <div style={{backgroundColor  :"white"}}>
      <img src='images/error.png' alt="404-error" />
      
      <Link to="/" >
        <h4 style={{color : "black"}}><u>Go To Login  </u></h4>
      </Link>
    </div>
  )
}

export default Exception