import axios from 'axios';
import React, { useEffect, useState } from 'react'
import toast from 'react-hot-toast';
import { Row, Col, Button, Modal, ModalBody, ModalFooter } from 'reactstrap';
import { url } from '../api/URLbackend';
import NavbarStudent from '../components/NavbarStudent'
import QuizCard from './QuizCard'

const CoursesStudent = () => {

    const [assignment, setAssignment] = useState();
    const [notes, setNotes] = useState(true);
    const [quiz, setQuiz] = useState();
    const [notesLink, setNotesLink] = useState(0)
    const [assignmentQuestion, setAssignmentQuestion] = useState()

    useEffect(() => {
        axios.get(url + `showallAssign`)
            .then((res) => {
                setAssignmentQuestion(res.data);
            })

        axios.get(url + `showallnoteslink`)
            .then((res) => {
                setNotesLink(res.data)
            })
            .catch((err) => {
                toast.error("Something wents wrong")
            })

    }, [])

    const AssignmentTogg = () => {
        setAssignment(!false)
        setQuiz(false)
        setNotes(false)
    }
    const NotesTogg = () => {
        setAssignment(false)
        setNotes(!false)
        setQuiz(false)
    }
    const QuizTogg = () => {
        setAssignment(false)
        setNotes(false)
        setQuiz(!false)
    }

    const [modal, setModal] = useState(false);
    const toggle = () => setModal(!modal);

    return (
        <div>
            <div className="cont container-fluid p-3 d-flex justify-content-center align-items-center">
                <div className="row" style={{ backgroundColor: "#1f153c", width: "21em" }}>
                    <NavbarStudent />
                </div>
                <div className="main col-9 d-flex align-items-center flex-column">
                    <div className="upp ">

                    </div>
                    <div className=" low ">
                        <h2 className="d-flex justify-content-center mt-2 ">Courses &nbsp; <i class="bi bi-bookmark-star text-success"></i></h2>
                        <div className="col-12 mt-5 d-flex" >
                            <div className="card ms-5 d-flex align-items-center " style={{ width: "14em", height: "32em", border: "none" }}>
                                <div className="me-5 btn btn-primary  d-flex justify-content-center align-items-center fw-bold " style={{ height: "3em", width: "10em", borderRadius: "1rem", marginTop: "2em" }} onClick={() => AssignmentTogg(!false)} >Assignment</div>
                                <div className="me-5 btn btn-success  d-flex justify-content-center align-items-center fw-bold " style={{ height: "3em", width: "10em", borderRadius: "1rem", marginTop: "2em" }} onClick={() => NotesTogg()}>Notes</div>
                                <div className="me-5 btn btn-warning  d-flex justify-content-center align-items-center fw-bold " style={{ height: "3em", width: "10em", borderRadius: "1rem", marginTop: "2em" }} onClick={() => QuizTogg()}>Quiz</div>
                            </div>
                            <div className='ms-4' style={{ width: "50em" }}>
                                {
                                    !assignment ? <></> :
                                        <div style={{ marginTop: "2em" }}>
                                            <Button className='btn-outline-info' onClick={toggle}>Check All Assingement </Button>
                                            <Modal isOpen={modal} toggle={toggle} >
                                                <ModalBody >
                                                    <table className="table">
                                                        <thead>
                                                            <tr className='table-primary fw-normal'>
                                                                <th style={{ width: "3em", color: "black" }} scope="col">Assignment No </th>
                                                                <th style={{ width: "15em", color: "black" }} scope="col">Details</th>
                                                            </tr>
                                                        </thead>
                                                        <tbody >

                                                            {
                                                                assignmentQuestion.filter(e => parseInt(localStorage.getItem("studentId")) === e.sid.studentId).sort((a, b) => { return a.assignNo - b.assignNo }).map((data) => {
                                                                    return (
                                                                        <tr key={data.assignNo}>
                                                                            <th style={{ color: "black" }} scope="row"> {data.assignNo} </th>
                                                                            <td style={{ color: "black", wordBreak: "break-word" }}> {data.question} </td>
                                                                        </tr>
                                                                    )
                                                                })
                                                            }

                                                        </tbody>
                                                    </table>
                                                </ModalBody>
                                                <ModalFooter>
                                                    <Row style={{ height: "3em" }}>
                                                        <Col>
                                                            <Button color="primary" onClick={toggle}>Okay! </Button>
                                                        </Col>
                                                    </Row>
                                                </ModalFooter>
                                            </Modal>
                                        </div>
                                }
                                {
                                    notesLink === 0 ? <></> :
                                        !notes ? <></> : <table className="mt-5 table table-bordered">
                                            <thead>
                                                <tr>
                                                    <th style={{ width: "3em", color: "black" }} scope="col">Sr.No </th>
                                                    <th style={{ width: "15em", color: "black" }} scope="col">Subject </th>
                                                    <th scope="col">
                                                        <span style={{ color: "black" }}>Link to Drive</span>
                                                    </th>
                                                    <th style={{ width: "15em", color: "black" }} scope="col"> Copy to clipboard </th>
                                                </tr>
                                            </thead>
                                            <tbody >

                                                {
                                                    notesLink.map((data) => {
                                                        return (
                                                            <tr key={data.subId}>
                                                                <th scope="row" style={{ color: "black" }}>{data.subId}</th>
                                                                <td style={{ color: "black" }}>{data.subName}</td>
                                                                <td>
                                                                    <p style={{ color: "blue" }} >{data.subLink}</p>
                                                                </td>
                                                                <td style={{ color: "black" }} onClick={() => { navigator.clipboard.writeText(`${data.subLink}`); toast.success("Link Copied !!!") }} c> <i class="bi bi-clipboard-check"></i></td>
                                                            </tr>
                                                        )
                                                    })
                                                }

                                            </tbody>
                                        </table>
                                }
                                {
                                    !quiz ? <></> :
                                        <>
                                            <QuizCard />
                                        </>
                                }
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </div>
    )
}
export default CoursesStudent